<template>

   <v-layout row wrap>

    
  <div id="informe" class="box-style">
  
  <v-card class="white card-act pb-4">
        

        <v-card-text class="text-md-center text-sm-center text-xs-center header_act">

            <v-layout d-block>
            
            
             <v-form ref="form">
              <v-flex xs12 md12 sm12 class="text-md-center mt-4">
       
       <v-flex md12 black--text text-md-center text-xs-center text-sm-center>
      <h1 class="text text-sub--itemsub weitng mb-2"></h1>
      <v-text-field append-icon="mic" id="search" v-model="placa" class="mx-1 input_seach mb-2" flat solo-inverted hide-details label="Buscar placa" prepend-inner-icon="search"></v-text-field>
    </v-flex>

            
             
              </v-flex>
           </v-form>
         </v-layout>
      </v-card-text>
     <v-divider></v-divider>
     <v-card-actions>
     <v-spacer></v-spacer>
     <v-flex d-flex text-md-right>
         <v-btn  color="blue darken-1"  @click="validador()">Buscar</v-btn>
     </v-flex>
     </v-card-actions>
     </v-card> 
  </div>
   <div id="informe" class="box-style">
     <h3 class="text text-sub--itemsub  blue--text  label-item ">RESULTADO</h3>
   <v-data-table
      :headers="headers"
      :items="registros"
    
      class="elevation-1 table-activate"
    >
      <template v-slot:items="props">
        <td class="text text-sub--itemsub">{{ props.item.id }}</td>
        <td class="text text-sub--itemsub">{{ props.item.placa }}</td>
        <td class=" text text-sub--itemsub">{{ props.item.fecha_entrada}}</td>
        <td class=" text text-sub--itemsub">{{ props.item.fecha_salida}}</td>
        <td class="text text-sub--itemsub"><v-btn @click="imprimir(props.item.id)" class="btn-act orange">Imprimir</v-btn></td>
        
     
        


      </template>
    </v-data-table>


   
    </div>

  
<!--FIN SALIDA VEHICULO -->


<v-snackbar
         id="modalig"
         class="mss-modal  v-snack--active2"
         v-model='show'
         top

       >
       <v-flex  md12 d-flex>
           <span class="text text-sub--item  text-md-center text-xs-center">{{texto}}</span>
       </v-flex>
       
     
    </v-snackbar>

<v-dialog
      v-model="dialog"
         persistent
      width="300"
    >
      <v-card
        color="primary"
        dark
      >
        <v-card-text>
          Buscando ...
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>

  
</v-layout>


</template>
<script>
 
  export default {
    
    data () {
      return {
        headers: [
          { text: 'Id', value: 'id' },
          { text: 'Placa', value: 'placa' },
          { text: 'Hora Entrada', value: 'valor' },
          { text: 'Hora Salida', value: 'tarifa' },
            { text: 'Imprimir', value: 'imprimir' },
           ],
       placa: "",
       registros : [],
       dialog : false,
       timeout:4000,
       show:false,
       mascara : "000000000",
      }
		},
    
		methods:{

     mascarac(valor){
       var c = valor.toString();
       var t = c.length;
       var res = this.mascara.substr(0, ((this.mascara.length)-t));
       res += c;
       return res;
      },

    validador()
    {
     
    if (this.placa !== "")
        {
          this.busqueda();
        }
    else
      {
        this.texto = "No puede exister campos vacios";
         this.show = true;
      }

         
       
    },

   async  busqueda()
    {
           
         this.dialog = true;
          var vm = this;
          this.placa = this.placa.toUpperCase();
         await this.axios({
           method: 'post',
         //  url: 'http://localhost:4000/admin/reimpresion',
            url: 'http://107.21.217.207:8000/admin/reimpresion',
            data: {
               placa : vm.placa,
            }
          }).then(function (response) {
              vm.registros = response.data.info;
              vm.dialog = false;
              console.log("consulta: " + vm.registros);
          })
    
      },

      buscar_id(id)
      {
        const buscado = (element) => element.id == id ;
        var pos = this.registros.findIndex(buscado); 
        return pos;
      },


      async imprimir(id)
      {
        
        var pos =   this.buscar_id(id);
          var m = this.mascarac(this.registros[pos].id);
          var formData = new FormData();
          var datos = new Object();
          datos.placa   = this.registros[pos].placa;
          datos.entrada = this.registros[pos].fecha_entrada;
          datos.modalidad = this.registros[pos].concepto;
          datos.consecutivo = m;
          datos.salida =  this.registros[pos].fecha_salida;
          datos.valor =   this.registros[pos].valor_cobrado;
          datos.iva = this.registros[pos].iva;
          datos.duracion = this.registros[pos].tiempo_transcurrido;
          datos = JSON.stringify(datos);
          formData.append('paquete',datos);
           if (this.registros[pos].estado == '2')
            {
         await this.axios.post("http://107.21.217.207:80/impresiontermica/salida.php",formData)
         .then(function(response)
               {
                 console.log("tiquete salida");
               });
            }
          else
           {
             await this.axios.post("http://107.21.217.207:80/impresiontermica/ticket.php",formData)
         .then(function(response)
               {
                 console.log("tiquete entrada");
               });
            }

           
        }
      
  }
  }
</script>
<style >
	#comprar{
		border-radius: 20px;
	}
</style>
